.cr_popular {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: var(--font-family);
    background-color: var(--color-header);
}
.cr_popular-title{
    color: var(--color-text2);
    font-size: 30px;
    margin-bottom: 1rem;
}
.cr_popular-text{
    color: var(--color-text1);
    font-size: 22px;
    padding: 1rem 0;
   
}
.cr_popular-text .p2{
    display:flex;
    justify-content: center;
}
.cr_popular-feature {
    margin-top: 2rem;
    display: flex;
}